  font-weight: 500;
  white-space: pre-line;
  line-height: 1.5;
  max-height: 70vh;
  overflow-y: auto;
}

.floating-message .success-message {
  background: linear-gradient(rgba(40, 167, 69, 0.85), rgba(40, 167, 69, 0.85)), var(--dark-surface);
  color: white;
  border: 1px solid rgba(40, 167, 69, 0.3);
}

.floating-message .error-message {
  background: linear-gradient(rgba(240, 80, 80, 0.15), rgba(240, 80, 80, 0.15)), var(--dark-surface);
  color: #e57373;
  border: 1px solid rgba(240, 80, 80, 0.3);
}

.message-actions {
